<script>
	export default {
		globalData: {  
			isDiscountActive: false,
			isDiscountYHJ: false, //优惠券
			isDiscountSF: false ,//顺丰9折,
			flag:"",
			KYImg:""
		},
		onLaunch: function() {
			// #ifdef MP-WEIXIN
				const updateManager = uni.getUpdateManager();
				updateManager.onCheckForUpdate(function (res) {
				  // 请求完新版本信息的回调
				  console.log("请求完新版本信息的回调:",res.hasUpdate);
				});
				updateManager.onUpdateReady(function (res) {
					uni.showModal({
						title: '更新提示',
						content: '新版本已经准备好，是否重启应用？',
						success(res) {
							if (res.confirm) {
								// 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
								updateManager.applyUpdate();
							}
						}
					});
				});
				updateManager.onUpdateFailed(function (res) {
				  // 新的版本下载失败
				  console.log("新的版本下载失败");
				});
				if(uni.getStorageSync('wxauthorize') != "" && uni.getStorageSync('wxauthorize') != undefined){
					this.authorize_mp_wx();
				}
			// #endif
			//用户首次进入需提示活动
			// let discountSF = uni.getStorageSync('isDiscountSF');
			// let discount = uni.getStorageSync('isDiscountYHJ');
			// if(discountSF == "true" && discount == "true") return false;
			// if(discountSF == "true"){
			// 	this.globalData.isDiscountActive = true;
			// 	this.globalData.isDiscountYHJ = true;
			// 	uni.setStorage({key: "isDiscountYHJ", data: "true"});
			// }else{
			// 	this.globalData.isDiscountActive = true;
			// 	this.globalData.isDiscountSF = true;
			// 	uni.setStorage({key: "isDiscountSF", data: "true"});
			// }
		},
		onShow: function() {
			console.log('App Show')
		},
		onHide: function() {
			console.log('App Hide')
		},
		methods: {
			isToken: function(){
				const that = this;
				// #ifdef H5
				if(uni.getStorageSync("h5initlog") == "" || uni.getStorageSync("h5initlog") == undefined){
					if(that.isWechat()){
						uni.getStorage({
							key: 'token',
							fail: function(){
								// if(that.$util.IsPC()){
								// 	window.location.href = "/pages/logs/logs";
								// }else{
									that.authorize_H5();
								// }
							}
						});
					}else{
						if(that.$util.IsPC()){
							window.location.href = this.apiHost + "/page/pc/ysdpc-template/";
						}else{
							uni.getStorage({
								key: 'token',
								fail: function(){
									window.location.href = "/pages/logs/logs";
								}
							});
						}
					}
				}
				// #endif
			},
			//H5授权登录
			authorize_H5: function(){
				let that = this;
				var pages = getCurrentPages();
				var wx_h5_code = (pages[pages.length - 1]).$route.query.code;
				wx_h5_code = wx_h5_code == undefined?'' : wx_h5_code;
				var $route = window.location.href;
				var validCode = uni.getStorageSync("validCode");//防止重复刷新
				that.$util.isGoLogin = false;
				//如果拿到code，调用授权接口，没有拿到就跳转微信授权链接获取
				if(validCode != "1" || !that.$util.isNotEmpty(wx_h5_code) ){
					uni.setStorage({key: 'validCode', data: "1"});
					let _url = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${that.wxappid}&redirect_uri=${encodeURIComponent(window.location.href)}&response_type=code&scope=snsapi_userinfo&state=1#wechat_redirect`
					window.location.href = _url;
				}else{
					that.$util._h5_login('logs', wx_h5_code);
				}
			},
			//小程序授权登录
			authorize_mp_wx: function(){
				let that = this;
				//判断用户是否授权
				wx.getSetting({
					success (res) {
						if(res.authSetting["scope.userInfo"] == true){
							//已授权
							console.log('已授权');
							that.$util._mp_wx_login("logs");
						}
						if(res.authSetting["scope.userInfo"] == false||res.authSetting["scope.userInfo"] == undefined){
							// 未授权
							console.log('未授权');
							that.$util.isGoLogin = false;
							uni.navigateTo({url: "/pages/logs/logs?state=false"});
						}
					}
				})
			},
			isWechat: function() {
				var ua = navigator.userAgent.toLowerCase();
				if (ua.match(/MicroMessenger/i) == "micromessenger") {
					return true;
				} else {
					return false;
				}
			}
		}
	}
</script>

<style>
	/*每个页面公共css */
	@import './common/css/iconfont.css';
	@import './common/css/style.css';
</style>
